import Api from '@/common/api';
export default {
	data() {
		return {
			keyword: '',
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			hotProductList: [],
			brandList: [], // 推荐品牌
			homeFlashPromotion: [], // 当前秒杀场次
			subjectList: [], //推荐专题
			cat_list: [],
			homeFlashPromotion: [],
			groupHotGoodsList: [],
			newProductList: []
		};
	},

	onLoad() {
		this.loadData();
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			this.logining = true;
			let params = {};
			let groupHotGoodsList = await Api.apiCall('get', Api.index.homeFlashPromotionList, params);
			this.groupHotGoodsList = groupHotGoodsList;

			let advertiseList = await Api.apiCall('get', Api.index.bannerList, params);
			console.log(advertiseList);
			this.carouselList = advertiseList || [];
			this.swiperLength = this.carouselList.length;
			this.logining = false;
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		navToTabPage(url) {
			uni.navigateTo({
				url: url
			});
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/secskillDetail?id=${id}`
			});
		},
		navToList() {
			uni.navigateTo({
				url: `/pages/product/list`
			});
		}
	},

	search() {
		uni.navigateTo({
			url: '/pages/search/search'
		});
	},

	// 标题栏input搜索框点击
	onNavigationBarSearchInputClicked: async function(e) {
		uni.navigateTo({
			url: '/pages/search/search'
		});
	},
	//点击导航栏 buttons 时触发
	onNavigationBarButtonTap(e) {
		const index = e.index;
		if (index === 0) {
			this.$api.msg('点击了扫描');
		} else if (index === 1) {








			uni.navigateTo({
				url: '/pages/notice/notice'
			});
		}
	}
};
